<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<!-- <el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers"></el-button>
				</el-form-item> -->
				<el-form-item>
					<el-button type="primary" size="big" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="classlist" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
		<!-- 	<el-table-column type="selection" width="55">
			</el-table-column> -->
			<<!-- el-table-column type="index" width="60">
			</el-table-column> -->
			<el-table-column prop="id" label="id" width="100">
			</el-table-column>
			<el-table-column prop="title" label="" width="120">
			</el-table-column>
			<el-table-column prop="banner" label="banner" width="140">
				<template scope="scope">
					<img class="banner-img" :src="scope.row.banner">	
				</template>
			</el-table-column>
			<el-table-column prop="desc" label="" width="200">
			</el-table-column>
			<el-table-column prop="tag" label="" width="100">
				<template scope="scope">
					<el-tag type="success">{{scope.row.tag}}</el-tag>	
				</template>
			</el-table-column>
			<el-table-column prop="peoples" label="" min-width="100">
			</el-table-column>
			<el-table-column prop="prize" label="" min-width="100">
			</el-table-column>
			<el-table-column style="text-align:center;" label="" width="100">
				<template scope="scope">
					<el-col :span="24">
						<el-button class="btn" type="primary" size="small" @click="goEditClassIndex(scope.row)"></el-button>
					</el-col>
					<el-col :span="24">
						<el-button class="btn" type="success" size="small" @click="gotoClassDetail(scope.row)"></el-button>
					</el-col>
					<el-col :span="24">
						<el-button class="btn" type="info" size="small" @click="gotoFreeList(scope.row)"></el-button>
					</el-col>
				</template>
			</el-table-column>
			<el-table-column label="" width="100">
				<template scope="scope">
					<el-col :span="24">
						<el-button class="btn" type="primary" size="small" @click="handleEdit(scope.row)"></el-button>
					</el-col>
					<el-col :span="24">
					<el-button type="danger" class="btn" size="small" @click="handleDel(scope.row)"></el-button>
					</el-col>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<!-- <el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col> -->

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="title">
					<el-input v-model="editForm.title" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label=""  prop="desc">
					<el-input v-model="editForm.desc" type="textarea" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="tag">
					<el-input v-model="editForm.tag" auto-complete="off"></el-input>
				</el-form-item>
				<!--  -->
				<el-form-item label="banner" prop="banner">
					<el-upload
					  class="upload-demo"
					  action="https://jsonplaceholder.typicode.com/posts/"
					  :on-preview="handlePreview"
					  :on-remove="handleRemove"
					  :file-list="fileList2">
					  <el-button size="small" type="primary"></el-button>
					 <!--  <div slot="tip" class="el-upload__tip">jpg/png500kb</div> -->
					</el-upload>
				</el-form-item>
				<el-form-item label="" prop="prize">
					<el-input-number v-model="editForm.prize" auto-complete="off"></el-input-number>
				</el-form-item>
				<el-form-item label="" prop="peoples">
					<el-input-number v-model="editForm.peoples" auto-complete="off"></el-input-number>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="" prop="title">
					<el-input v-model="addForm.title" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label=""  prop="desc">
					<el-input v-model="addForm.desc" type="textarea" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="tag">
					<el-input v-model="addForm.tag" auto-complete="off"></el-input>
				</el-form-item>
				<!--  -->
				<el-form-item label="banner" prop="banner">
					<el-upload
					  class="upload-demo"
					  action="https://jsonplaceholder.typicode.com/posts/"
					  :on-preview="handlePreview"
					  :on-remove="handleRemove"
					  :file-list="fileList2">
					  <el-button size="small" type="primary"></el-button>
					 <!--  <div slot="tip" class="el-upload__tip">jpg/png500kb</div> -->
					</el-upload>
				</el-form-item>
				<el-form-item label="" prop="prize">
					<el-input-number v-model="addForm.prize" auto-complete="off"></el-input-number>
				</el-form-item>
				<el-form-item label="" prop="peoples">
					<el-input-number v-model="addForm.peoples" auto-complete="off"></el-input-number>
				</el-form-item>	
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getClassList,editClass,addClass,removeClass } from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					name: '',
					title:''
				},
				classlist:[],
				users: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					// title: [
					// 	{ required: true, message: '', trigger: 'blur' }
					// ],
					// desc: [
					// 	{ required: true, message: '', trigger: 'blur' }
					// ],
					// prize:[
					// 	{ required: true, message: '', trigger: 'blur' }
					// ],
				},
				//
				editForm: {
					id: 0,
					title: '',
					banner: '',
					desc: '',
					tag: '',
					peoples: 0,
					prize:0,
				},

				addFormVisible: false,//
				addLoading: false,
				addFormRules: {
					// title: [
					// 	{ required: true, message: '', trigger: 'blur' }
					// ],
					// desc: [
					// 	{ required: true, message: '', trigger: 'blur' }
					// ],
					// prize:[
					// 	{ required: true, message: '', trigger: 'blur' }
					// ],
				},
				//
				addForm: {
					id: 0,
					title: '',
					banner: '',
					desc: '',
					tag: '',
					peoples: 0,
					prize:0,
				},
				// 
				fileList2:[]

			}
		},
		methods: {


			getClassList(){
				let para = {
					name:this.filters.title
				}
				this.listLoading = true;
				getClassList({}).then( res => {
					this.classlist = res.data.classList;
					this.listLoading = false;
				} )
			},

			//
			handleDel: function (row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					removeClass(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getClassList();
					});
				}).catch(() => {

				});
			},
			//
			handleEdit: function (row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
				this.fileList2[0] = {name:'banner',url:row.banner}
			},
			//
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					id: 0,
					title: '',
					banner: '',
					desc: '',
					tag: '',
					peoples: 0,
					prize:0,
				};
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							editClass(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getClassList();
							});
						});
					}
				});
			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							addClass(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getClassList();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			handleRemove(file, fileList) {
		        console.log(file, fileList);
		    },
		    handlePreview(file) {
		       console.log(file);
		     },
		     goEditClassIndex(row){
		     	this.$router.push({path:`/classIndex/${row.id}`});
		     },
		     gotoClassDetail(row){
		     	this.$router.push({path:`/classDetail/${row.id}`});
		     },
		     gotoFreeList(row){
		     	this.$router.push({path:`/freeList/${row.id}`});
		     }
		},
		mounted() {
			this.getClassList();
		}
	}

</script>

<style scoped>
	.banner-img{
		width:100%;
		height:100%;
		vertical-align:middle;
	}
	.btn{
		margin:5px 0;
	}
</style>	


